<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
// import config from "@/utils/config";
export default {
  components: {
    HeadTop
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      balance: 0,
      swiperOptions: {
        pagination: {
          el: '.swiper-pagination'
        },
      },
      isShowExchange: false,
      isShowExtract: false,
      initContract: () => {
        data.initData()
      },
      initData: () => {
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const headerSwiperHandle = (index) => {
      console.log(index)
    }
    const isShowExchangeHandle = () => {
      data.isShowExchange = true
    }
    const isShowExtractHandle = () => {
      data.isShowExtract = true
    }
    const refData = toRefs(data);
    return {
      ...refData,
      headerSwiperHandle,
      isShowExchangeHandle,
      isShowExtractHandle
    };
  }
}
</script>
